<template>
  <div id="app">
    <div class="top" v-if="isNavBar">
      <van-nav-bar
        :title="title"
        @click-left="onClickLeft"
      >
      <template #left>
          <van-icon name="arrow-left" v-if="isBack" size="18" />
        </template>
        <template #right>
          <van-icon :name="icon" size="18" />
        </template>
      </van-nav-bar>
    </div>
    <div class="center" :style="styleSheet">
      <router-view></router-view>
    </div>
    <div class="bottom" v-if="isTabBar">
      <van-tabbar v-model="active"  @change="changeRoute">
        <van-tabbar-item v-for="(item, index) in icons" :key="index">
          <span>{{ item.title }}</span>
          <template #icon="props">
            <img
              :src="props.active ? icons[index].active : icons[index].inactive"
            />
          </template>
        </van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
export default {
  name: "app",
  components: {},
  data() {
    return {
      active: 0,
      icons: [
        {
          title: "首页",
          active: "https://wap.365msmk.com/img/home-active.87af5fd4.png",
          inactive:
            "data:image/png;base64,",
        },

        {
          title: "课程",
          active: "https://wap.365msmk.com/img/home-active.87af5fd4.png",
          inactive:
            "data:image/png;base64,",
        },

        {
          title: "约课记录",
          active: "https://wap.365msmk.com/img/home-active.87af5fd4.png",
          inactive:
            "data:image/png;base64,",
        },

        {
          title: "练习",
          active: "https://wap.365msmk.com/img/home-active.87af5fd4.png",
          inactive:
            "data:image/png;base64,",

        },

        {
          title: "我的",
          active: "https://wap.365msmk.com/img/home-active.87af5fd4.png",
          inactive:
            "data:image/png;base64,",
        },
      ],
    };
  },
  computed: {
    isNavBar() {
      // console.log(this.$route.meta);
      return this.$route.meta.isNavBar;
    },
    isTabBar() {
      // console.log(this.$route.meta);
      return this.$route.meta.isTabBar;
    },
    icon() {
      // console.log(this.$route.meta);
      var icon = "";
      icon = this.$route.meta.icon;
      return icon;
    },
    title() {
      var title = "";
      title = this.$route.meta.title;
      return title;
    },
    isBack(){
      let isBack = false;
      isBack = this.$route.meta.isBack;
      return isBack;
    },
    styleSheet(){
      var styleSheet = {};
      var num = 100
      if (this.isNavBar == true) {
        num = num - 8
      }
      if (this.isTabBar == true) {
        num = num - 8
      }
      return {
        height:`${num}vh`
      }
    }
  },
  methods: {
    onClickLeft() {
      // console.log("左侧被点击");
      this.$router.go(-1);
    },

    changeRoute() {
      switch (this.active) {
        case 0:
          this.$router.push({ path: "/" });
          break;
        case 1:
          this.$router.push({ path: "/Appointment" });
          break;
        case 2:
          this.$router.push({ path: "/order" });
          break;
        case 3:
          this.$router.push({ path: "/practice" });
          break;
        case 4:
          this.$router.push({ path: "/mine" });
          break;
      }
    },
  },
};
</script>

<style>
html,
body,
#app {
  width: 100%;
  height: 100%;
  list-style: none;
  font-size: 0.16rem;
  background-color: #f0f2f5;
}
* {
  margin: 0;
  padding: 0;
}
.top {
  height: 8vh;
}
.center {
  overflow: scroll;
}
.bottom {
  height: 8vh;
}
</style>
